:root {
  color-scheme: light;
}

.professional-profile {
  max-width: 1200px;
  margin: 0 auto 96px;
  padding: 32px 24px 0;
  font-family: "Poppins", system-ui, -apple-system, "Segoe UI", sans-serif;
  color: #111827;
}

.profile-cover {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 36px;
  padding: 40px;
  border-radius: 32px;
  background: #f7f8fb;
  border: 1px solid rgba(15, 23, 42, 0.08);
  align-items: center;
}

.cover-media {
  border-radius: 50%;
  overflow: hidden;
  width: 220px;
  height: 220px;
  background: #e5e7eb;
  justify-self: center;
  box-shadow: 0 15px 35px rgba(15, 23, 42, 0.18);
}

.cover-media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.cover-copy {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.eyebrow-tag {
  align-self: flex-start;
  font-size: 0.78rem;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  font-weight: 600;
  padding: 6px 12px;
  border-radius: 999px;
  background: rgba(15, 23, 42, 0.08);
  color: #0f172a;
}

.cover-copy h1 {
  margin: 0;
  font-size: clamp(2.1rem, 1.4rem + 1.4vw, 3rem);
}

.cover-headline {
  margin: 0;
  font-size: 1rem;
  font-weight: 600;
  color: #0f172a;
}

.cover-lead {
  margin: 0;
  font-size: 1.1rem;
  line-height: 1.6;
  color: #475569;
}

.cover-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-top: 12px;
}

.primary-cta,
.secondary-cta,
.book-service-btn {
  border: none;
  border-radius: 999px;
  padding: 0.85rem 1.8rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.25s ease, color 0.25s ease, transform 0.2s ease;
}

.primary-cta,
.book-service-btn {
  background: #111111;
  color: #ffffff;
  box-shadow: 0 12px 24px rgba(17, 17, 17, 0.2);
}

.primary-cta:hover:not(:disabled),
.book-service-btn:hover:not(:disabled) {
  background: #333333;
  transform: translateY(-2px);
}

.primary-cta:disabled,
.book-service-btn:disabled {
  background: rgba(148, 163, 184, 0.35);
  color: #475569;
  cursor: not-allowed;
  box-shadow: none;
  transform: none;
}

.secondary-cta {
  background: transparent;
  color: #0f172a;
  border: 2px solid #0f172a;
}

.secondary-cta:hover {
  background: #0f172a;
  color: #ffffff;
}

.profile-story {
  margin-top: 48px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 20px;
}

.story-card {
  padding: 26px;
  border-radius: 24px;
  background: #ffffff;
  border: 1px solid rgba(226, 232, 240, 0.9);
  box-shadow: 0 12px 32px rgba(15, 23, 42, 0.08);
}

.story-card h3,
.story-card h4 {
  margin: 0 0 10px;
}

.story-card p {
  margin: 0;
  color: #475569;
  line-height: 1.6;
}

.contact-card {
  background: #fdfcfa;
}

.profile-guidelines {
  margin-top: 48px;
  padding: 32px;
  border-radius: 28px;
  background: #ffffff;
  border: 1px solid rgba(203, 213, 225, 0.6);
}

.profile-guidelines header h3 {
  margin: 12px 0 4px;
}

.guideline-list {
  margin-top: 18px;
  display: grid;
  gap: 12px;
}

.guideline-list article {
  padding: 16px 18px;
  border-radius: 18px;
  background: #f7f9fc;
  border: 1px solid rgba(226, 232, 240, 0.8);
}

.guideline-list p {
  margin: 0;
  color: #1e293b;
  line-height: 1.5;
}

.services-list {
  margin-top: 56px;
}

.services-list header h3 {
  margin: 10px 0 6px;
  font-size: 1.6rem;
}

.services-list header p {
  margin: 0;
  color: #475569;
}

.services-list ul {
  list-style: none;
  padding: 0;
  margin: 32px 0 0;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.service-row {
  display: flex;
  align-items: center;
  gap: 24px;
  padding: 24px;
  border-radius: 24px;
  background: #ffffff;
  border: 1px solid rgba(226, 232, 240, 0.9);
  box-shadow: 0 10px 30px rgba(15, 23, 42, 0.08);
  flex-wrap: wrap;
}

.service-media {
  flex: 0 0 160px;
  height: 120px;
  border-radius: 20px;
  overflow: hidden;
  background: #f1f5f9;
  box-shadow: inset 0 0 0 1px rgba(15, 23, 42, 0.04);
}

.service-media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.service-details {
  flex: 1 1 260px;
  min-width: 200px;
}

.service-row h4 {
  margin: 0 0 8px;
  font-size: 1.2rem;
}

.service-row p {
  margin: 0 0 10px;
  color: #475569;
  line-height: 1.5;
}

.service-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 14px;
  font-size: 0.95rem;
  color: #0f172a;
  font-weight: 600;
}

.book-service-btn {
  align-self: center;
  margin-left: auto;
}

.empty-state {
  text-align: left;
}

@media (max-width: 640px) {
  .profile-cover {
    padding: 28px;
  }

  .cover-media {
    width: 180px;
    height: 180px;
  }

  .service-row {
    flex-direction: column;
    align-items: flex-start;
  }

  .service-media {
    width: 100%;
    height: 180px;
  }

  .book-service-btn {
    width: 100%;
    text-align: center;
    margin-left: 0;
  }
}
